#animated-abacus {
  height: 100%;
  width: 100%;
  enable-background: new 0 0 817 439;

  .st0 {
    fill: none;
    stroke: $black-background-color;
    stroke-miterlimit: 10;
  }
  .st1 {
    fill: none;
    stroke: $black-background-color;
    stroke-linecap: round;
    stroke-miterlimit: 10;
  }
  .st2 {
    fill: $black-background-color;
    stroke: #ffffff;
    stroke-linecap: round;
    stroke-linejoin: bevel;
    stroke-miterlimit: 10;
  }
  .bars {
    // fill: $primary-color;
    stroke: $abacus-darker-gold;
    stroke-miterlimit: 10;
  }

  .bead {
    fill: rgba(255, 255, 255, 1);
    stroke: rgba(0, 0, 0, 0.2);
    stroke-miterlimit: 0;
  }
  #Front_Box {
    .front {
      // stroke: $primary-color;
      fill: $abacus-darker-gold;
    }
    .middlebar {
      fill: #ffffff;
      stroke: white;
      stroke-miterlimit: 10;
    }
    .backbar {
      fill: none;
      stroke: $primary-color;
      stroke-miterlimit: 10;
    }
    .top-right-line {
      fill: none;
      stroke: $primary-color;

      stroke-miterlimit: 10;
    }
  }
}