@import "override";
@import "variables";
@import "abacus";

.MuiTab-textColorPrimary {
  color: white;
}
.gradient-button {
  background: linear-gradient(90deg, #ffbb00 0%, #ffd34e 100%);
  box-shadow: none !important;
}
.theme-underline {
  width: 90px;
  height: 3px;
  margin: 3.5% 0;
  background-color: $primary-color;
}

#main-navigation {
  padding: 1% 4%;
  background-color: $black-background-color;
  border-bottom: none;

  #logo {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 50px;
  }
}

#intro {
  background-color: $black-background-color;
  color: white;
  min-height: 100vh;
  padding: 0 4%;
  padding-bottom: 4%;

  .theme-underline {
    margin-top: 10%;
    margin-bottom: 5%;
  }
  #intro-typography {
    text-align: left;
    margin-top: 4%;

    button {
      border-radius: 15px;
      margin: 4% 0;
      color: rgba(0, 0, 0, 0.6);
    }
  }
  #button-group {
    margin-top: 0;
  }
  #tiles {
    padding: 1%;
    border-radius: 7px;
    background-color: rgba(255, 255, 255, 0.05);

    .flippy-container {
      min-width: 20%;

      .feature-icon {
        font-size: 3rem;
        margin-bottom: 5%;
        color: #ffbb00;
      }
      .feature-name {
        font-size: 1.75rem;
      }
      .flippy-front,
      .flippy-back {
        cursor: pointer;
        text-align: center;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        padding: 1%;
        height: 12rem;
        border-radius: 25px;

        background: $black-background-color;
        color: white;
      }
      .flippy-back {
        color: $font-black;
        background: white;
      }
    }
  }
}

#our-approach {
  min-height: 50vh;
  padding: 4% 4% 4% 10%;
  h4 {
    margin-bottom: 4%;
  }

  .approach {
    margin-bottom: 30%;

    .MuiTypography-gutterBottom {
      margin-bottom: 4%;
    }
    button {
      border-radius: 15px;
      color: rgba(0, 0, 0, 0.6);
    }
  }

  #approach-img-container {
    position: sticky;
    top: 0;
    bottom: 0;
    min-height: 80vh;
  }
}

#news {
  padding: 4%;
  background-color: $grey-background-color;
  color: black;
  h4 {
    margin-bottom: 4%;
  }
  .news-container {
    border-radius: 7px;
    box-shadow: 0 1px 2px 1px rgba(0, 0, 0, 0.1);
    height: 500px;
    width: 350px;
    background-color: white;

    h5 {
      padding: 8% 0;
      padding-bottom: 4%;
    }

    .theme-underline {
      margin: 5% 0;
      margin-bottom: 10%;
    }

    .cover-img {
      background-repeat: no-repeat;
      background-size: cover;
      height: 220px;
      width: 100%;
    }
    .content {
      padding: 0 6%;
      text-align: left;
    }
  }
}

#team {
  padding: 4%;
  background-color: $black-background-color;

  #headshot-container {
    margin-top: 6.5%;
    .headshot {
      position: relative;
      img {
        margin-bottom: 2%;
        position: absolute;
        bottom: 58%;
        border-radius: 50%;
        border: 2px solid $primary-color;
      }
      .headshot-content {
        color: white;
        width: 100%;
        border-radius: 5px;
        display: flex;
        padding-top: 75px;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        // box-shadow: 0 1px 2px 1px rgba(0, 0, 0, 0.1);
        padding-bottom: 4%;
      }
    }
  }
}
